@Injectable()
export class i18nService {

  language = [];

  constructor(private app: AppBootstrapService){
    this.registerLanguageFile('cn', cn);
    this.registerLanguageFile('en', en);
  }

  registerLanguageFile(name: string, file: any){
    this.language.push({'name': name, 'file': file});
  }

  applyLanguage(name: string): boolean {
    let language = this.findLanguageByName(name);
    if (null!=language){
      this.app.defaultLaunchApp(language.file, language.name);
      return true;
    }
    return false;
  }

  private findLanguageByName(name: string):any {
    for (let i=0; i< this.language.length; i++) {
      if (this.language[i].name === name)
        return this.language[i];
    }
    return null;
  }
}


