import { Injectable, TRANSLATIONS, TRANSLATIONS_FORMAT, LOCALE_ID, NgZone } from '@angular/core';
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';
import { AppModule } from './app.module';

@Injectable()
export class AppBootstrapService {

    constructor(private ngZone: NgZone){

    }

    public defaultLaunchApp(transaction: any, locale_id: string) {
        this.launchApp(AppModule, transaction, "xlf", locale_id);
    }
    
    public launchApp(appModule: any, transaction: any, format: string, locale_id: string) {
        this.ngZone.runOutsideAngular(() => {
            platformBrowserDynamic().bootstrapModule(appModule, {providers: [
                {provide: TRANSLATIONS, useValue: transaction},
                {provide: TRANSLATIONS_FORMAT, useValue: format},
                {provide: LOCALE_ID, useValue: locale_id}
            ]});
        });
    }
}


