import { Component, ViewChild, ElementRef, Inject, HostListener } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';
import { en } from './messages.en';
import { cn } from './messages.cn';
import { TRANSLATIONS_FORMAT, LOCALE_ID } from '@angular/core';
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';
import { AppModule } from './app.module';
import { i18nService } from './i18n.service';
import { AppBootstrapService } from './appbootstrap.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  //title = 'app';
  LanguagesDisplay = false;
  currentLanguage: string;
  @ViewChild("language")
  language: ElementRef;

  constructor(@Inject(DOCUMENT) private document: any, private i18nService: i18nService){
    this.currentLanguage = document['locale'];
  }

  showLanguages(){
    this.LanguagesDisplay = true;
    console.log(this.LanguagesDisplay);
  }

  hideLanguages(){
    this.LanguagesDisplay = false;
  }

  changeLanguage(language: string){
    document['locale'] = language;
    this.i18nService.applyLanguage(language);
  }

  @HostListener('window:click', ['$event'])
  onClick($event: any) {
    let children = this.language.nativeElement.parentNode.childNodes;
    let contains: boolean = false;
    for(let i=0;i<children.length;i++){
      if (children[i].contains(event.target))
        contains = true;
    }
    if (!contains)
      this.hideLanguages();
  }
}



